body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.App > nav {
  min-width: 15em;
  height: 100%;
  padding: 0 1em 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: auto;
  color: #3b3b3b;
}

.App > nav a {
  color: inherit;
}

.App > nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.App > nav ul li ul {
  padding: 0 0 0 1em;
  border-left: 1px solid #eee;
  -webkit-transition: border 0.25s;
  transition: border 0.25s;
}

.App > nav ul li ul:hover {
  border-left: 2px solid rgb(219, 219, 219);
  color: rgb(41, 41, 41);
}

.App > nav > ul h5 {
  margin: 1.2em 0 .5em;
  font: inherit;
  font-size: 1.1em;
}

.App > nav ul {
  margin-bottom: 1em;
}

.App > main .item {
  margin-bottom: 2.5em;
}

.App > main .item h5 {
  font: inherit;
}

.App > main {
  height: 100%;
  overflow: auto;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App > main pre {
  max-width: 50em;
}

.App > main p {
  max-width: 40em;
}

.App > main pre {
  white-space: pre-wrap;  
}

.App a {
  color: #1a1a1a;
  text-decoration: none;
}

.App a:hover {
  text-decoration: underline;
}

.App a:hover > *::after {
  content: " \B6";
  text-decoration: none;
  display: inline-block;
}

.App > main .module > a h5 {
  font-size: 1.25em;
}

.App pre {
  background: #eee;
  padding: 1em;
  box-sizing: border-box;
}
